<template>
  <el-container>
    <el-header>
      <div class="header">
        <div class="title">企业名称：{{companyName}}</div>
      </div>
      <div class="" >
          <el-button type="primary" class="btn" @click="addServes">服务单新增</el-button>
          <el-button type="primary" class="btn">服务单导入</el-button>
      </div>
    </el-header>
    <el-main>
      <el-table :data="servesInfoList">
        <el-table-column prop="id" label="服务单ID">
        </el-table-column>
        <el-table-column prop="userName" label="被服务人姓名">
        </el-table-column>
        <el-table-column prop="mobile" label="被服务人电话">
        </el-table-column>
        <el-table-column prop="mobile" label="关联权益人">
        </el-table-column>
        <el-table-column prop="mobile" label="所属商品">
        </el-table-column>
        <el-table-column prop="beginTimeValue" label="生效起期">
        </el-table-column>
        <el-table-column prop="endTimeValue" label="生效至期">
        </el-table-column>
        <el-table-column prop="statusValue" label="生效状态">
        </el-table-column>
        <el-table-column label="操作" width='220'>
          <template slot-scope="scope">
            <el-button @click="edit(scope.row)" type="text" size="small">编辑</el-button>
            <el-button type="text" size="small" @click="delete(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="demonstration-box">
        <!-- <span class="demonstration">显示总数</span> -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="pageParams.size"
          layout="total, prev, pager, next"
          :total="pageParams.totalCount"
        ></el-pagination>
      </div>
    </el-main>
  </el-container>
</template>
<script>

const queryString = require('querystring')
import page from 'data/page'
import queryList from 'utils/queryList'
export default {
  name: 'servesList',
  mixins: [page],
  data() {
    return {
      companyName: this.$route.query.companyName,
      id: this.$route.query.id,
      servesInfoList: [],
      currentPage: 1
    }
  },
  created() {
    this.search()
  },
  methods: {
    addServes() {
      this.$router.push('addOrEditServe?type=add' + '&companyName=' + this.companyName + '&companyId=' + this.id)
    },
    edit(item) {
      this.$router.push('addOrEditServe?type=edit&id=' + item.id + '&companyName=' + this.companyName + '&companyId=' + this.id)
    },
    delete(item) {
      this.axios.delete(`order/${item.id}`).then(
        res => {
          const code = Number(res.data.code)
          if (code === 200) {
            this.$message(this.mes.success)
          } else {
            this.$message(this.mes.busy)
          }
        }
      ).catch(
        err => {
          this.$message(this.mes.err)
        }
      )
    },
     getStatusValue(val) {
          let value = ''
          switch (val) { // 0正常，1中止，2终止，3失效
            case '0': value = '正常'; break;
            case '1': value = '中止'; break;
            case '2': value = '终止'; break;
            case '3': value = '失效'; break;
          }
          return value
        },
    search() {
      
      const param = {
        companyid: this.id,
        page: this.currentPage - 1,
        size: 20
      }
      this.axios.get('/order?' + queryString.stringify(param)).then((res) => {
        const code = Number(res.data.code)
        if (code === 200) {
          this.servesInfoList = res.data.data.list
          this.servesInfoList.forEach((servesInfo, index) => {
            this.servesInfoList[index].beginTimeValue = this.timeFormater(servesInfo.beginTime).format('YYYY-MM-DD')
            this.servesInfoList[index].endTimeValue = this.timeFormater(servesInfo.endTime).format('YYYY-MM-DD')
            this.servesInfoList[index].statusValue = this.getStatusValue(String(servesInfo.status))
          })
          this.setPage(res.data.data)
        } else {
          this.$message(this.mes.busy)
        }
      }).catch((err) => {
        console.log(err)
        this.$message(this.mes.busy)
      })
    }
  }
}
</script>
<style lang="less" scoped>
  .header {
    text-align: left;
    border-bottom: 1px solid #eee;
    margin-bottom: 10px
  }
  .com-input {
    width: 210px;
    margin-right: 20px
  }
  .btn{
    margin-left: 20px;
  }
</style>

